@import url("https://fonts.googleapis.com/css2?family=Chakra+Petch:ital,wght@0,300;0,400;0,600;1,500&display=swap");

html {
  box-sizing: border-box;
  min-height: 100vh;
  background-color: rgb(19, 174, 177);
  background: linear-gradient(
    0deg,
    rgba(34, 193, 195, 1) 0%,
    rgb(16, 144, 146) 100%
  );
  margin: 0;
}
body {
  margin: 0;
  font-family: "Chakra Petch", sans-serif;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
  min-height: 100vh;
  flex: 1 1 100vh;
}

h1,
h2 {
  color: white;
  font-size: 3rem;
}
h1 {
  margin: 0;
  padding: 12px;
  width: 100%;
  background-color: #d68fd6;
}
h2 {
  padding: 8px;
  border-radius: 8px;
  background-color: white;
  color: rgb(19, 174, 177);
  font-size: 2rem;
  margin-left: 8px;
  margin-right: 8px;
}

@keyframes slide-bottom {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(4px);
    transform: translateY(4px);
  }
}

.form {
  display: flex;
  padding: 24px 28px;
  height: 100px;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  flex: 0 1 160px;
  margin: 0 8px 28px;
  background: white;
  border-radius: 8px;
  box-shadow: 5px 5px 8px -5px rgba(0, 0, 0, 0.48);
}

.form > * {
  font-family: inherit;
  margin-top: 12px;
  padding: 8px 20px;
  min-width: 14rem;
  border-radius: 20px;
  border: 3px solid #13aeb1;
  box-shadow: 5px 5px 8px -5px rgba(0, 0, 0, 0.48);
  outline: none;
  background-color: white;
  font-size: 1.3rem;
  font-weight: 600;
  color: #272727;
}
.form > *:first-child {
  margin-top: 0;
}

option {
  text-align: center;
}
/* .form > *:focus-visable {
  outline: none;
} */

input {
  text-align: center;
}
button {
  transform: translateY(0);
  display: flex;
  justify-content: center;
  align-items: center;
}

button:hover {
  border: 3px solid #d68fd6;
  background-color: #d68fd6;
  color: white;
  cursor: pointer;
}

button:active {
  background-color: #c987c9;
  color: white;
  cursor: pointer;
  animation-name: slide-bottom;
  animation-duration: 0.1s;
  animation-timing-function: ease-in;
  transform: translateY(1px);
  box-shadow: 5px 5px 12px -5px rgba(0, 0, 0, 0.63);
}

.background {
  position: relative;
  z-index: -1;
  height: 250px;
  width: 250px;
  background-color: #dbc1db;
  border-radius: 50%;
  box-shadow: 11px 10px 13px -8px rgba(0, 0, 0, 0.48);
}

footer {
  /* position: fixed; */
  bottom: 0;
  background-color: #d68fd6;
  color: white;
  width: 100%;
  margin: 0;
  padding: 10px;
  font-size: 1rem;
  justify-self: stretch;
}
img {
  height: 250px;
  position: relative;
  top: -250px;
  margin-bottom: -225px;
  border-radius: 0 0 50% 50%;
}
.transparent {
  background-color: transparent;
  box-shadow: none;
}
.void {
  flex: 1 1 0px;
}

.error {
  font-size: 2rem;
  padding: 12px;
  background-color: white;
  color: red;
  font-weight: 600;
  border-radius: 8px;
}
